import { Component, OnInit, Inject } from '@angular/core';
import { TableMasterService } from '../table-master.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { TableMaster } from '../table-master';

@Component({
  selector: 'app-edit-table-master',
  templateUrl: './edit-table-master.component.html',
  styleUrls: ['./edit-table-master.component.css']
})
export class EditTableMasterComponent implements OnInit {

  constructor(private tableMasterService: TableMasterService,
    @Inject(MAT_DIALOG_DATA) public data: TableMaster,
    public dialogRef: MatDialogRef<EditTableMasterComponent>) { }

  ngOnInit() {
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
  editTableMaster() {
    this.tableMasterService.updateTableMaster(this.data).subscribe((data) => {
      alert("Edit Successfully");
      this.dialogRef.close();
    }, (error) => {
      console.log("Don't Edit")
    })
  }
}
