import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA } from '../../../../../../../node_modules/@angular/material';
import { TableMasterService } from '../table-master.service';
import { MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-delete-table-master',
  templateUrl: './delete-table-master.component.html',
  styleUrls: ['./delete-table-master.component.css']
})
export class DeleteTableMasterComponent implements OnInit {

  constructor(private tableMasterService: TableMasterService,
    public dialogRef: MatDialogRef<DeleteTableMasterComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit() {
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDeleteTable() {
    this.tableMasterService.deleteTableMaster(this.data.tableMasterId).subscribe((data) => {
      alert("Delete Successfully");
      this.dialogRef.close();
    }, (error) => {
      console.log("Don't Delete");
    })
  }
}
