import { Component, OnInit, Inject } from '@angular/core';
import { TableMaster } from '../table-master';
import { TableMasterService } from '../table-master.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
@Component({
  selector: 'app-add-table-master',
  templateUrl: './add-table-master.component.html',
  styleUrls: ['./add-table-master.component.css']
})
export class AddTableMasterComponent implements OnInit {

  private tableMaster: TableMaster;

  constructor(private tableMasterService: TableMasterService,
    @Inject(MAT_DIALOG_DATA) public data: TableMaster,
    public dialogRef: MatDialogRef<AddTableMasterComponent>) { }

  ngOnInit() {
    this.tableMaster = new TableMaster();
  }

  saveTableMaster() {
    this.tableMasterService.createTableMaster(this.tableMaster).subscribe((data) => {
      alert("Save Successfully");
      this.dialogRef.close();
    }, (error) => {
      alert("Don't Save");
    })
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
}
