import { Component, OnInit,ViewChild,ElementRef } from '@angular/core';
import {TableMasterService} from './table-master.service';
import {TableMaster} from './table-master';
import {MatTableDataSource,MatDialog,MatPaginator,MatSort} from '@angular/material';
import {Router} from '@angular/router';
import {AddTableMasterComponent} from './add-table-master/add-table-master.component';
import {DeleteTableMasterComponent} from './delete-table-master/delete-table-master.component';
import {EditTableMasterComponent} from './edit-table-master/edit-table-master.component';
@Component({
  selector: 'app-table-master',
  templateUrl: './table-master.component.html',
  styleUrls: ['./table-master.component.css']
})
export class TableMasterComponent implements OnInit {

  private tableMaster:TableMaster[];
  dataSource;
  displayedColumns = ['tableMasterid', 'tabletype','actions'];
  constructor(private tableMasterService:TableMasterService,
    private router:Router,
    private _dialog: MatDialog) { }

  ngOnInit() {
    this.getAllTableMaster();
  }

  @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort:MatSort;
    @ViewChild('filter') filter: ElementRef;


    applyFilter(filterValue: string) {
      filterValue = filterValue.trim(); // Remove whitespace
      filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
      this.dataSource.filter = filterValue;
    }
  getAllTableMaster(){
    this.tableMasterService.getAllTableMaster().subscribe((data)=>{
      this.tableMaster=data;
      this.dataSource = new MatTableDataSource(this.tableMaster);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    },(error)=>{

    });
  }
  refresh(){
    this.getAllTableMaster();
  }
  saveTableMaster() {
    const dialogRef = this._dialog.open(AddTableMasterComponent, {
      data: {}  
    });
    dialogRef.afterClosed().subscribe(result => {
        this.getAllTableMaster();
    });
  }
  deleteTable(i: number, tableMasterId: number,tableType: string ) {
    const dialogRef = this._dialog.open(DeleteTableMasterComponent, {
      data: { tableMasterId: tableMasterId,tableType:tableType}
    });
    dialogRef.afterClosed().subscribe(result => {
        this.getAllTableMaster();
    });
  }
  editTable(i: number, tableMasterId: number,tableType: string ) {
    const dialogRef = this._dialog.open(EditTableMasterComponent, {
      data: { tableMasterId: tableMasterId,tableType:tableType}
    });
    dialogRef.afterClosed().subscribe(result => {
        this.getAllTableMaster();
    });
  }
}
